const initialState = {
  loading: false,
  error: "",
  success: "",
};

const componentReducer = createSlice({
  name: "componentReducer",
  initialState,
  reducers: {},
  extraReducers: {
    [getComponentList.fulfilled]: (state, action) => {},
    [getComponentList.pending]: (state, action) => {},
    [getComponentList.rejected]: (state, action) => {},
    [getComponentDetails.fulfilled]: (state, action) => {},
    [getComponentDetails.pending]: (state, action) => {},
    [getComponentDetails.rejected]: (state, action) => {},
    [updateComponentDetails.fulfilled]: (state, action) => {},
    [updateComponentDetails.pending]: (state, action) => {},
    [updateComponentDetails.rejected]: (state, action) => {},
    [getComponentBasic.fulfilled]: (state, action) => {},
    [getComponentBasic.pending]: (state, action) => {},
    [getComponentBasic.rejected]: (state, action) => {},
    [editComponentBasic.fulfilled]: (state, action) => {},
    [editComponentBasic.pending]: (state, action) => {},
    [editComponentBasic.rejected]: (state, action) => {},
    [createComponentList.fulfilled]: (state, action) => {
      // state.loading = false;
      // if (action.payload.data.error) {
      //   state.error = action.payload.data.error;
      //   state.success = "";
      // } else {
      //   //console.log(action.payload);
      //   //state.component = action.payload;
      //   state.success = "";
      // }
    },
    [createComponentList.pending]: (state, action) => {
      // console.log(action);
      // state.loading = true;
      // state.component = null;
      // state.success = "";
      // state.error = "";
    },
    [createComponentList.rejected]: (state, action) => {
      // console.log(action);
      // state.loading = false;
      // if (action.payload.error) {
      //   state.component = null;
      //   state.error = action.payload.error;
      // }
    },
    [updateComponentList.fulfilled]: (state, action) => {
      state.componentLoading = false;
      if (action.payload.error) {
        state.componentError = action.payload.error;
        state.componentSuccess = "";
      } else {
        state.component = action.payload;
        state.success = "";
      }
    },
    [updateComponentList.pending]: (state, action) => {
      state.componentLoading = true;
      state.component = null;
      state.componentSuccess = "";
      state.componentError = "";
    },
    [updateComponentList.rejected]: (state, action) => {
      state.componentLoading = false;
      if (action.payload.error) {
        state.component = null;
        state.componentError = action.payload.error;
      }
    },
    [deleteComponent.fulfilled]: (state, action) => {
      state.componentLoading = false;
      if (action.payload.error) {
        state.componentError = action.payload.error;
        state.componentSuccess = "";
      } else {
        state.component = action.payload;
        state.success = "";
      }
    },
    [deleteComponent.pending]: (state, action) => {
      state.componentLoading = true;
      state.component = null;
      state.componentSuccess = "";
      state.componentError = "";
    },
    [deleteComponent.rejected]: (state, action) => {
      state.componentLoading = false;
      if (action.payload.error) {
        state.component = null;
        state.componentError = action.payload.error;
      }
    },
  },
});

export default componentReducer.reducer;
